.pricing-page .faq-row{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin:5% 0;
    padding:0 10%;
}
.pricing-page .faq-row .block{
    width: 30%;
    h1{
        font-size: 40px;
    }
    p{
        font-size: 18px;
        margin:25px 0 35px;
        a{
            color:#427cff;
        }
    }
    .help-center{  
       font-size: 18px;
       border:1px solid #427CFF;
       color:#427CFF;
       padding:10px 30px;
       border-radius: 20px;
    }
}

.pricing-page .faq-row .faqs{
    width: 50%;
    .single-question{
        border:1px solid #ddd;
        padding:20px 30px;
        margin:20px 0;
        border-radius: 5px;
        .the-question{
            display: flex;
            align-items: center;
            justify-content: space-between;
            cursor: pointer;
        }
        svg{
            font-size: 25px;
            color:#427cff;
            margin-left: 20px;
        }
        h6{
            font-size: 18px;
            color:#333;
            font-weight: 600;
        }
        .the-answer{
            margin-top: 15px;
            font-size: 18px;
        }
        .line{
            width: 25%;
            height: 2px;
            background-color: #ccc;
            margin-bottom: 15px;
        }
    }
}

@media(max-width:1401px){
    .pricing-page .faq-row .block{
        width: 32.5%;
        h1{
            font-size: 32.5px;
        }
        p{
            font-size: 15px;
            margin:20px 0 30px;
        }
        .help-center{  
           font-size: 15px;
        }
    }
    
    .pricing-page .faq-row .faqs{
        width: 50%;
        .single-question{
            padding:15px 25px;
            margin:17.5px 0;
            svg{
                font-size: 22px;
                margin-left: 15px;
            }
            h6{
                font-size: 15px;
            }
            .the-answer{
                font-size: 15px;
            }
        }
    }    
}

@media(max-width:767px){
    .pricing-page .faq-row{
        display: block;
        margin:35px 0;
        padding:0 7.5%;
    }
    .pricing-page .faq-row .block{
        width: 100%;
        margin:35px 0;
        text-align: center;
        h1{
            font-size: 20px;
        }
        p{
            font-size: 14px;
        }
        .help-center{  
           font-size: 14px;
           padding:9px 30px;
        }
    }
    
    .pricing-page .faq-row .faqs{
        width: 100%;
        margin:35px 0;
        .single-question{
            padding:12.5px 20px;
            margin:12.5px 0;
            svg{
                font-size: 20px;
                margin-left: 15px;
            }
            h6{
                font-size: 14px;
            }
            .the-answer{
                margin-top: 12.5px;
                font-size: 14px;
            }
            .line{
                margin-bottom: 12.5px;
            }
        }
    }
}

.pricing-page .faq-row .block{
    .help-center{
        display: inline-block;
        vertical-align: middle;
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
        position: relative;
        background: #fff;
        -webkit-transition-property: color;
        transition-property: color;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
    }
      .help-center:before{
        content: "";
        position: absolute;
        z-index: -1;
        top: -1px;
        left: -1px;
        right: -1px;
        bottom: -1px;
        border-radius: 20px;
        background: #427cff;
        -webkit-transform: scale(0);
        transform: scale(0);
        -webkit-transition-property: transform;
        transition-property: transform;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-timing-function: ease-out;
        transition-timing-function: ease-out;
    }
    .help-center:hover,.help-center:focus, .help-center:active {
        color: white;
    }
    .help-center:hover:before, .help-center:focus:before, .help-center:active:before {
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}

//===========================================
.pricing-page .shared-get-started{
    margin:0 !important;
}
.pricing-page .last-row{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 7.5%;
    margin:5% 0 0;
    .img{
        content: url('../../../../assets/Pricing/laptop.png');
        width: 50%;
    }
    .items3{
        width: 40%;
    }
    .item3{
        border:1px solid #ddd;
        border-radius: 10px;
        width: 75%;
        position: relative;
        padding:55px 50px 40px;
        &:last-child{
            margin:75px 0 15px auto;
        }
    }
    .profile{
        width: 80px;
        position: absolute;
        top:-30px;
        left: -30px;
    }
    .stars svg{
        color:#aaa;
        margin:0 1px;
    }
    p{
        font-size: 18px;
        margin:10px 0 15px
    }
    h6{
        font-size: 17px;
    }
}

@media(max-width:1401px){
    .pricing-page .last-row{
        .item3{
            padding:40px 20px 20px 35px;
            &:last-child{
                margin:60px 0 10px auto;
            }
        }
        .profile{
            width: 65px;
            top:-27px;
            left: -27px;
        }
        .stars svg{
            margin:0 1px;
            font-size: 15px;
        }
        p{
            font-size: 15px;
            margin:10px 0 15px
        }
        h6{
            font-size: 14px;
        }
    }
}

@media(max-width:776px){
    .pricing-page .shared-get-started{
        margin:0 !important;
    }
    .pricing-page .last-row{
        display: block;
        padding:0 7.5%;
        margin:45px 0 0;
        .img{
            width: 330px;
            max-width: 100%;
            margin:35px auto;
        }
        .items3{
            width: 100%;
        }
        .item3{

            width: 85%;
            padding:25px 10px 15px 30px;
            &:last-child{
                margin:50px 0 15px auto;
            }
        }
        .profile{
            width: 55px;
            top:-20px;
            left: -20px;
        }
        .stars svg{
            margin:0 1px;
            font-size: 14px;
        }
        p{
            font-size: 14px;
        }
        h6{
            font-size: 13px;
        }
    }
}


@-webkit-keyframes hvr-icon-push {
    50% {
      -webkit-transform: scale(0.5);
      transform: scale(0.5);
    }
}
@keyframes hvr-icon-push {
    50% {
      -webkit-transform: scale(0.5);
      transform: scale(0.5);
    }
}
.pricing-page .last-row .items3{
    .item3{
       
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        &:hover{
            .stars svg{
                color:rgb(247, 243, 8);
            }
        }
    }
    .item3 .profile{
        -webkit-transform: translateZ(0);
        transform: translateZ(0);
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-property: transform;
        transition-property: transform;
        -webkit-transition-timing-function: ease-out;
        transition-timing-function: ease-out;
    }
    .item3:hover .profile, .item3:focus .profile, .item3:active .profile {
        -webkit-animation-name: hvr-icon-push;
        animation-name: hvr-icon-push;
        -webkit-animation-duration: 0.3s;
        animation-duration: 0.3s;
        -webkit-animation-timing-function: linear;
        animation-timing-function: linear;
        -webkit-animation-iteration-count: 1;
        animation-iteration-count: 1;
    }
}