.pricing-page .included-plans{
    margin:7.5% 0 5%;
    padding:0 12.5%;
    h1{
        font-size: 40px;
        text-align: center;
    }
    .devices{
        position: relative;
    }
    .row1{
        background-color: #f8faff;
        border-radius: 10px;
        padding:45px 55% 45px 7.5%;
        margin:50px 0 25px;
        svg{
            font-size: 40px;
        }
        h6{
            font-size: 20px;
            margin:15px 0;
        }
        p{
            font-size: 18px;
        }
    }
    .fetaures{
        font-size: 18px;
        a{
            color:#427CFF;
        }
    }
    .img{
        content: url('../../../../assets/Pricing/img.png');
        width: 35%;
        position: absolute;
        right: 0;
        bottom:-5%;
        transition: 0.35s ease-in-out;
        &:hover{
            transform: scale(1.035);
        }
    }
}

.pricing-page .small-cards-container{
    margin:5% 0;
    padding:0 12.5%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .svg-card{
        width: 28%;
        padding:20px 25px;
        margin:15px 0;
    }
}

@media(max-width:1401px){
    .pricing-page .included-plans{
        h1{
            font-size: 32.5px;
        }
        .row1{
            padding:40px 55% 40px 7.5%;
            margin:50px 0 25px;
            svg{
                font-size: 35px;
            }
            h6{
                font-size: 17.5px;
                margin:15px 0;
            }
            p{
                font-size: 15px;
            }
        }
        .fetaures{
            font-size: 15px;
        }
        .img{
            width: 37.5%;
            bottom:-5%;
        }
    }
    
    .pricing-page .small-cards-container{
        .svg-card{
            width: 28%;
            padding:15px 20px;
            margin:15px 0;
        }
    }
}

@media(max-width:991px){
    .pricing-page{
        .included-plans, .small-cards-container{
            padding:0 7.5%;
            .img{
                width: 45%;
            }
        }
        .svg-card{
            width: 29%;
        }
    }
}

@media(max-width:767px){
    .pricing-page .included-plans{
        margin:40px 0;
        padding:0 7.5%;
        h1{
            font-size: 20px;
        }
        .row1{
            padding:30px 20px;
            margin:20px 0;
            svg{
                font-size: 30px;
            }
            h6{
                font-size: 16px;
                margin:15px 0;
            }
            p{
                font-size: 14px;
            }
        }
        .fetaures{
            font-size: 14px;
        }
        .img{
            width: 330px;
            max-width: 100%;
            position: relative;
            right: unset;
            bottom:unset;
            margin:30px auto 15px;
        }
    }
    
    .pricing-page .small-cards-container{
        margin:20px 0;
        padding:0 8.5%;
        .svg-card{
            width: 47.5%;
            padding:15px 20px;
            margin:10px 0;
        }
    }
    
}

@media(max-width:475px){
    .pricing-page .small-cards-container{
        .svg-card{
            width: 100%;
        }
    }
}

//================================================================
.pricing-page .items2{
    margin:5% 0;
    padding:0 10%;
    display: flex;
    justify-content: space-between;
    .item2{
        width: 47.5%;
        padding:30px 55px;
        border-radius: 10px;
        border:1px solid #ddd;
        display: flex;
        align-items: center;
    }
    .img{
        width: 22.5%;
        margin-right: 25px;
    }
    .block{
        flex: 1;
    }
    h6{
        font-size: 20px;
    }
    p{
        font-size: 18px;
        margin:15px 0 30px;
    }
    a{
        font-size: 18px;
        border:1px solid #427CFF;
        color:#427CFF;
        padding:10px 30px;
        border-radius: 20px;
        &:hover{
            background-color: #427CFF;
            color:#fff;
        }
    }
}

@media(max-width:1401px){
    .pricing-page .items2{
        .item2{
            padding:20px 30px;
        }
        .img{
            width: 22.5%;
            margin-right: 20px;
        }   
        h6{
            font-size: 17.5px;
        }
        p{
            font-size: 15px;
            margin:15px 0 30px;
        }
        a{
            font-size: 15px;
            padding:9.5px 30px 10.5px;
            border-radius: 20px;
        }
    }
}

@media(max-width:991px){
    .pricing-page .items2{
        padding:0 5%;
        .item2{
            width: 48.5%;
        }
    }
}

@media(max-width:767px){
    .pricing-page .items2{
        margin:30px 0;
        padding:0 7.5%;
        display: block;
        .item2{
            width: 100%;
            padding:20px;
            margin:15px 0;
        }
        .img{
            width: 25%;
        }
        h6{
            font-size: 15px;
        }
        p{
            font-size: 14px;
            margin:10px 0 20px;
        }
        a{
            font-size: 14px;
            padding:8px 30px 9px;
        }
    }
}

@-webkit-keyframes hvr-icon-wobble-vertical {
    16.65% {
      -webkit-transform: translateY(6px);
      transform: translateY(6px);
    }
    33.3% {
      -webkit-transform: translateY(-5px);
      transform: translateY(-5px);
    }
    49.95% {
      -webkit-transform: translateY(4px);
      transform: translateY(4px);
    }
    66.6% {
      -webkit-transform: translateY(-2px);
      transform: translateY(-2px);
    }
    83.25% {
      -webkit-transform: translateY(1px);
      transform: translateY(1px);
    }
    100% {
      -webkit-transform: translateY(0);
      transform: translateY(0);
    }
  }
  @keyframes hvr-icon-wobble-vertical {
    16.65% {
      -webkit-transform: translateY(6px);
      transform: translateY(6px);
    }
    33.3% {
      -webkit-transform: translateY(-5px);
      transform: translateY(-5px);
    }
    49.95% {
      -webkit-transform: translateY(4px);
      transform: translateY(4px);
    }
    66.6% {
      -webkit-transform: translateY(-2px);
      transform: translateY(-2px);
    }
    83.25% {
      -webkit-transform: translateY(1px);
      transform: translateY(1px);
    }
    100% {
      -webkit-transform: translateY(0);
      transform: translateY(0);
    }
  }
.pricing-page .items2{
    .item2{
        // display: inline-block;
        // vertical-align: middle;
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        &:hover{
            transform: scale(1.03);
        }
    }
    .item2 .img{
        -webkit-transform: translateZ(0);
        transform: translateZ(0);
    }
    .item2:hover .img{
        -webkit-animation-name: hvr-icon-wobble-vertical;
        animation-name: hvr-icon-wobble-vertical;
        -webkit-animation-duration: 1s;
        animation-duration: 1s;
        -webkit-animation-timing-function: ease-in-out;
        animation-timing-function: ease-in-out;
        animation-delay: 0.3s;
        -webkit-animation-delay: 0.3s;
        -webkit-animation-iteration-count: 1;
        animation-iteration-count: 1;
    }
}